import { useEffect, useState } from 'react'
import { useStateLocalStorage } from '../../Hooks/useStateLocalStorage'
import servicioCarrito from '../../servicios/carrito'
import basura from "./img/basura.svg";
import comprar from "./img/carrito-de-compras.svg";
import Modal from './modal.js';

import './Index.css'

import { useNavigate } from 'react-router'
import { accionSetCantidad } from '../../state/actions'
import { useDispatch } from 'react-redux'



export function Index() {
    const [carrito, setCarrito] = useStateLocalStorage('carrito', [])
    const [estatusCompra, setEstatusCompra] = useState(false)

    const navigate = useNavigate()
    const dispatch = useDispatch()
    // -------------------------------------------------------------------------------------
    // Efecto para actualizar la cantidad de productos en el carrito de forma global (redux)
    useEffect(() => {
        const cantidad = carrito.length
        dispatch(accionSetCantidad(cantidad))
    }, [carrito, dispatch])
    // -------------------------------------------------------------------------------------

    function costo() {
        const carritoClon = [...carrito];
        let precio = 0;
        carritoClon.forEach((e) => {
            precio += e.cantidad * e.precio;
        });
        return precio;
    }

    function decrementarItem(id) {
        console.log("decrementarItem", id);

        const carritoClon = [...carrito];
        const producto = carritoClon.find((p) => p.id === id);

        if (producto.cantidad > 1) {
            producto.cantidad--;
            setCarrito(carritoClon);
        }
    }

    function incrementarItem(id) {
        console.log("incrementarItem", id);

        const carritoClon = [...carrito];
        const producto = carritoClon.find((p) => p.id === id);

        if (producto.cantidad < producto.stock) {
            producto.cantidad++;
            setCarrito(carritoClon);
        }
    }

    function borrarItem(id) {
        console.log("borrarItem", id);

        if (
            window.confirm(
                `¿Está seguro de borrar el producto del carrito de id ${id}?`
            )
        ) {
            const carritoClon = [...carrito];
            const index = carritoClon.findIndex((p) => p.id === id);
            carritoClon.splice(index, 1);
            setCarrito(carritoClon);
        }
    }

    function borrarCarrito() {
        console.log("borrarCarrito");

        if (window.confirm(`¿Está seguro de borrar todo el carrito?`)) {
            setCarrito([]);
        }
    }

    async function generarPedido() {
        console.log("generarPedido");
        setEstatusCompra(true)
        const pedido = { pedido: carrito };
        
        await new Promise(r => setTimeout(r, 2000))      // delay de 2000mS
        
        console.log("Enviar pedido...");
        await servicioCarrito.enviar(pedido);
        console.log("Pedido recibido!");
        
        setCarrito([]);
        setEstatusCompra(false)
        navigate('/')
    }

    return (
        <div className="carrito">
            <h2>Carrito</h2>
            {estatusCompra && <Modal/>}
            {carrito.length > 0 && (
                <>
                    <div className="productos">
                        {carrito.map((producto, i) => (
                            <div key={i} className="producto">
                                <img src={producto.foto} alt="" />
                                <div className="cantidad">
                                    <button
                                        type="button"
                                        className="btn"
                                        onClick={() => {
                                            decrementarItem(producto.id);
                                        }}
                                    >
                                        -
                                    </button>
                                    <p>{producto.cantidad}</p>
                                    <button
                                        type="button"
                                        className="btn"
                                        onClick={() => {
                                            incrementarItem(producto.id);
                                        }}
                                    >
                                        +
                                    </button>
                                </div>
                                <p>{producto.nombre}</p>
                                <p>{producto.precio}</p>
                                <button
                                    className="botar"
                                    onClick={() => {
                                        borrarItem(producto.id);
                                    }}
                                >
                                    <img src={basura} alt="boton de borrar" />
                                </button>
                            </div>
                        ))}
                    </div>
                    <div className="conjunto">
                        <div className="cuenta">
                            <p>
                                Subtotal: <span>${costo()}</span>
                            </p>
                            <p>
                                Envio: <span>${0}</span>
                            </p>
                            <p>
                                Descuentos: <span>${0}</span>
                            </p>
                            <p>
                                Total: <span>${costo()}</span>
                            </p>
                        </div>
                        <div className="botones">
                            <button
                                onClick={() => {
                                    generarPedido();
                                }}
                            >
                                <img src={comprar} alt="boton pedir" />
                                Hacer pedido
                            </button>
                            <button
                                onClick={() => {
                                    borrarCarrito();
                                }}
                            >
                                <img src={basura} alt="boton de borrar" />
                                Borrar pedido
                            </button>
                        </div>
                    </div>
                </>
            )}
            {!carrito.length && <h2>No se encontraron pedidos para mostrar</h2>}
        </div>
    );
}
