import { useState } from "react";
import "./Index.css";
export function Index() {
	const [errorFormulario, getErrorFormulario] = useState({
		nombre: true,
		correo: true,
		comentario: true,
	});

	function sonClavesIguales(obj) {
		const claves = Object.values(obj);
		let resultado = claves.every((clave) => clave === "verificado");
		return resultado;
	}

	return (
		<div className="contacto">
			<h1>Contacto</h1>

			<div>
				<form
					onSubmit={(e) => {
						e.preventDefault();
						e.target.reset();
					}}
					className="alta-form"
				>
					<div className="input-group">
						<label htmlFor="nombre">nombre</label>
						<input
							id="nombre"
							type="text"
							name="nombre"
							placeholder="Luis Barreto"
							required
							onBlur={(e) => {
								const clave = /^[a-zA-Z\s]{3,15}$/;
								const comprobacion = clave.test(e.target.value);
								if (!comprobacion) {
									e.target.setCustomValidity(
										"Debe tener minimo 3 caracteres y solo se permiten letras"
									);
									e.target.reportValidity(
										"Debe tener minimo 3 caracteres y solo se permiten letras"
									);
									getErrorFormulario({
										...errorFormulario,
										nombre: "error",
									});
								} else {
									getErrorFormulario({
										...errorFormulario,
										nombre: "verificado",
									});
								}
							}}
						/>
					</div>

					<div className="input-group">
						<label htmlFor="email">email</label>
						<input
							id="email"
							type="email"
							name="email"
							placeholder="jone@gmail.com"
							required
							onBlur={(e) => {
								const clave =
									/^[a-zA-Z0-9.$#!\-=+_*&]{3,30}@[a-zA-Z.]{3,15}$/;
								const comprobacion = clave.test(e.target.value);
								if (!comprobacion) {
									e.target.setCustomValidity(
										"Correo electonico invalido"
									);
									e.target.reportValidity(
										"Correo electonico invalido"
									);
									getErrorFormulario({
										...errorFormulario,
										correo: "error",
									});
								} else {
									getErrorFormulario({
										...errorFormulario,
										correo: "verificado",
									});
								}
							}}
						/>
					</div>

					<div className="input-group">
						<label htmlFor="textarea">comentario</label>
						<textarea
							aria-required
							name="textarea"
							id="textarea"
							required
							placeholder="Deja tu comentario"
							onBlur={(e) => {
								const clave = /^.{10,280}$/;
								const comprobacion = clave.test(e.target.value);
								if (!comprobacion) {
									e.target.setCustomValidity(
										"Debe tener minimo 10 caracteres"
									);
									e.target.reportValidity(
										"Debe tener minimo 10 caracteres"
									);
									getErrorFormulario({
										...errorFormulario,
										comentario: "error",
									});
								} else {
									getErrorFormulario({
										...errorFormulario,
										comentario: "verificado",
									});
								}
							}}
						></textarea>
					</div>
					{sonClavesIguales(errorFormulario) && (
						<button>Enviar</button>
					)}
					{!sonClavesIguales(errorFormulario) && (
						<h3 className="errorEnFormulario">
							Por favor, verifique el formulario
						</h3>
					)}
				</form>
			</div>
		</div>
	);
}
